<template>
  <div id="home" v-bind:style="{ 'background': 'linear-gradient(0deg, rgba(37, 35, 35, 0.7682423311) 2%, rgba(255, 255, 255, 0) 100%), linear-gradient(180deg, rgba(37, 35, 35, 0.7682423311) 2%, rgba(255, 255, 255, 0) 100%), url(' + todosDados.img_desktop + ')' }">
    <!-- <div v-for="data in jsonEp" :key="data">{{data}}</div> -->
    <!-- {{dados[currentEp].img_desktop}} -->
    <!-- <img src="../assets/img/ep-1.jpg" alt=""> -->
    <header>
      <div class="container">
        <img class="logo" src="../assets/img/Logo.png" alt="Logo" />
        <nav>
          <ul>
            <li>
              <a href="">
                <span v-if="windowWidth > 576">Lista de episódios</span>
                <svg
                  v-else
                  width="14"
                  height="13"
                  viewBox="0 0 14 13"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0 3.71429H11.2V5.57143H0V3.71429ZM0 0H11.2V1.85714H0V0ZM0 7.42857H7.46667V9.28571H0V7.42857ZM9.33333 7.42857V13L14 10.2143L9.33333 7.42857Z"
                    fill="white"
                  />
                </svg>
              </a>
            </li>
            <li>
              <a class="btn-box" href="">
                <span v-if="windowWidth > 576">Box The Twilight Zone</span>
                <svg
                  v-else
                  width="16"
                  height="13"
                  viewBox="0 0 16 13"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M12.8 0L14.4 3.25H12L10.4 0H8.8L10.4 3.25H8L6.4 0H4.8L6.4 3.25H4L2.4 0H1.6C0.72 0 0.00799999 0.73125 0.00799999 1.625L0 11.375C0 12.2688 0.72 13 1.6 13H14.4C15.28 13 16 12.2688 16 11.375V0H12.8Z"
                    fill="#323232"
                  />
                </svg>
              </a>
            </li>
          </ul>
        </nav>
      </div>
      <picture class="cover-mobile">

        <!-- <img
          :src="require(`../assets/img/eps${imgMobile}`)"
          style="top: 0; max-width: 100%; position: absolute; z-index: 0"
          alt="Logo"
        /> -->
      </picture>
    </header>

    <div class="middle-infos">
      <div class="container">
        <div class="top">
          <p>{{ todosDados.title_en }}</p>

          <small v-if="windowWidth > 576">•</small>
          <div class="imdb" v-if="windowWidth > 576">
            <img src="../assets/img/icons/icon-imdb.png" alt="Logo IMDb" />
            <p>{{ todosDados.imdb}}</p>
          </div>

          <small>•</small>
          <p>T{{todosDados.season}}E{{todosDados.ep}}</p>
          <small>•</small>
          <p>{{todosDados.released}}</p>
        </div>
        <h1>{{ todosDados.title_pt }}</h1>
        <p class="description">
          {{ todosDados.description_pt }}
        </p>
      </div>
      <a class="btn-infos" href="">
        <img
          v-if="windowWidth > 576"
          src="../assets/img/icons/infos.svg"
          alt="Infos"
        />
        <svg
          v-else
          width="20"
          height="20"
          viewBox="0 0 20 20"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M10 0C4.48 0 0 4.48 0 10C0 15.52 4.48 20 10 20C15.52 20 20 15.52 20 10C20 4.48 15.52 0 10 0ZM11 15H9V9H11V15ZM11 7H9V5H11V7Z"
            fill="#323232"
          />
        </svg>

        <p>Mais Informações</p>
      </a>
    </div>

    <div class="bottom-infos">
      <div class="container">
        <div class="social">
          <a href="http://wa.me/5513981508643" target="_blank">
            <img src="../assets/img/icons/whatsapp.svg" alt="Link Whats" />
          </a>
          <a href="https://www.instagram.com/vitorcstaa/" target="_blank">
            <img src="../assets/img/icons/instagram.svg" alt="Link Insta" />
          </a>
          <a
            href="https://www.linkedin.com/in/jos%C3%A9-costa-3668601a4/"
            target="_blank"
          >
            <img src="../assets/img/icons/linkedin.svg" alt="Link Linkedin" />
          </a>
        </div>

        <div class="bottom-middle">
          <a class="first-last" @click.prevent="prevEp">
            <img src="../assets/img/icons/arrow-left.svg" alt="Arrow Left" />
          </a>
          <p>
            <input type="text" :value="`${currentEp}`" maxlength="3" />
            / 156
          </p>
          <a @click.prevent="nextEp">
            <img src="../assets/img/icons/arrow-right.svg" alt="Arrow Right" />
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import { episodes } from "../assets/episode.json";
// import axios from 'axios';

export default {
  data() {
    return {
      windowWidth: window.innerWidth,
      currentEp: 1,
      dados: {},
      lengthJson: 0,
      todosDados: {
        ep: "",
        season: "",
        title_en: "",
        description_en: "",
        title_pt: "",
        description_pt: "",
        img_desktop: "",
        img_mobile: "",
        released: "",
        imdb: "",
        director: "",
        writer: "",
        runtime: "",
        country: "",
        language: "",
        cast: []
      }
    };
  },

  methods: {
    onResize() {
      this.windowWidth = window.innerWidth;
    },
    prevEp() {
      if(this.currentEp == 1) return;
      this.currentEp--;
      this.refresh();
    },
    nextEp() {
      if(this.currentEp == this.lengthJson) return;
      this.currentEp++;
      this.refresh();
    },
    refresh(){
      this.todosDados.ep = this.dados[this.currentEp].ep;
      this.todosDados.season = this.dados[this.currentEp].season;
      this.todosDados.title_en = this.dados[this.currentEp].title_en;
      this.todosDados.description_en = this.dados[this.currentEp].description_en;
      this.todosDados.title_pt = this.dados[this.currentEp].title_pt;
      this.todosDados.description_pt = this.dados[this.currentEp].description_pt;
      this.todosDados.img_desktop = require(`../assets/img/${this.dados[this.currentEp].img_desktop}`);
      this.todosDados.img_mobile = require(`../assets/img/${this.dados[this.currentEp].img_mobile}`);
      this.todosDados.released = this.dados[this.currentEp].released;
      this.todosDados.imdb = this.dados[this.currentEp].imdb;
      this.todosDados.director = this.dados[this.currentEp].director;
      this.todosDados.writer = this.dados[this.currentEp].writer;
      this.todosDados.runtime = this.dados[this.currentEp].runtime;
      this.todosDados.country = this.dados[this.currentEp].country;
      this.todosDados.language = this.dados[this.currentEp].language;
      this.todosDados.cast = this.dados[this.currentEp].cast;
    }
  },

  created() {
    console.log("created");

    fetch("/episode.json")
      .then((r) => r.json())
      .then(
        (json) => {
          this.dados = json;

          this.refresh();
          

          this.lengthJson = Object.keys(json).length
        },
        (response) => {
          console.log("Error loading json:", response);
        }
      );
  },

  mounted() {
    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },

  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },

  computed: {
    // img() {
    //     return require(`../assets/img/${this.dados[this.currentEp].img_desktop}`);
    // }
  }
};
</script>

<style lang="scss">
.container {
  max-width: 1240px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@keyframes fromBottomOpacityHalf {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 0.5;
  }
}

@keyframes fromBottomOpacityHalfMobile {
  0% {
    transform: translateY(-30px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 0.5;
  }
}

@keyframes fromBottom {
  0% {
    transform: translateY(40px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fromTop {
  0% {
    transform: translateY(-40px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fromRight {
  0% {
    transform: translateX(40px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes fromLeft {
  0% {
    transform: translateX(-40px);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes opacity {
  0% {
    opacity: 0;
    transform: translateY(4px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

#home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-repeat: no-repeat !important;
  background-size: cover !important;
  background-position: center !important;
  height: 100vh;

  header {
    padding: 16px 0px;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .logo {
        animation: opacity 1.5s;
      }

      nav {
        ul {
          display: flex;
          justify-content: space-between;
          align-items: center;
          list-style: none;
          li {
            a {
              display: flex;
              color: #fff;
              padding: 10px 20px;
              text-decoration: none;
              animation: opacity 1.5s;
              &:hover {
                text-decoration: underline;
              }

              &.btn-box {
                background: #fff;
                color: #323232;
                margin-left: 8px;
                transition: all 0.25s;

                &:hover {
                  text-decoration: none;
                  background: #323232;
                  color: #fff;
                }
              }
            }
          }
        }
      }
    }

    .cover-mobile {
      display: none;
    }
  }

  .middle-infos {
    .top {
      display: flex;
      align-items: center;
      animation: fromTop 1s ease;
      p {
        font-weight: 700;
        color: #fff;
        font-size: 14px;
        letter-spacing: 0.04em;
      }

      .imdb {
        display: flex;
        align-items: center;
        img {
          width: 40px;
          margin-right: 4px;
        }
      }
      small {
        color: rgba(255, 255, 255, 0.5);
        margin: 0 10px;
        font-size: 14px;
      }
    }

    h1 {
      max-width: 700px;
      margin-top: 8px;
      font-size: 64px;
      color: #fff;
      animation: fromBottom 1s;
    }

    .description {
      max-width: 640px;
      line-height: 24px;
      margin-top: 10px;
      color: #fff;
      animation: fromBottom 1s;
    }

    .btn-infos {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      right: 0;
      bottom: 64px;
      background: #fff;
      width: 64px;
      height: 64px;
      padding-left: 24px;
      text-decoration: none;
      color: #323232;
      font-weight: 700;
      transition: all 0.25s;
      transition-delay: 0.05s;
      img {
        width: 24px;
        margin-right: 4px;
      }

      p {
        overflow: hidden;
        opacity: 0;
        white-space: nowrap;
        font-size: 14px;
        text-decoration: none;
        transition: all 0.25s;
      }

      &:after {
        content: "";
        position: absolute;
        top: -4px;
        right: 0;
        width: 68px;
        height: 70px;
        border-width: 1px 0 1px 1px;
        border-style: solid;
        border-color: #fff;
        transition: all 0.25s;
      }

      &:hover {
        width: 200px;
        padding-left: 16px;
        p {
          opacity: 1;
        }

        &:after {
          width: 204px;
        }
      }
    }
  }

  .bottom-infos {
    padding: 16px 0 32px 0;
    .container {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-start;

      .social {
        display: flex;
        align-items: center;
        a {
          opacity: 0.5;

          &:first-child {
            animation: fromBottomOpacityHalf 1s;
          }

          &:nth-child(2) {
            animation: fromBottomOpacityHalf 1.5s;
          }

          &:last-child {
            animation: fromBottomOpacityHalf 2s;
          }

          &:hover {
            opacity: 1;
          }
        }
      }

      a {
        display: flex;
        padding: 6px;
        transition: all 0.25s;

        img {
          width: 22px;
        }

        &:nth-child(2) {
          margin: 0 8px;
        }
      }

      .bottom-middle {
        position: absolute;
        display: flex;
        align-items: center;
        left: 50%;
        transform: translateX(-50%);

        a {
          align-items: center;
          justify-content: center;
          width: 34px;
          height: 34px;
          opacity: 1;
          cursor: pointer;

          img {
            width: 10px;
          }

          &:hover {
            background: rgba(255, 255, 255, 0.05);
          }

          &:nth-child(1) {
            animation: fromLeft 1s;
          }

          &:nth-child(3) {
            animation: fromRight 1s;
          }

          &.first-last {
            opacity: 0.3;
            &:hover {
              background: rgba(255, 255, 255, 0.03);
            }
          }
        }

        p {
          color: #fff;
          margin: 0 16px;
          font-size: 18px;
          animation: opacity 1s;
          input {
            width: 32px;
            text-align: right;
            background: none;
            color: #fff;
            border: none;
            font-size: 18px;
            outline: none;

            &:focus {
              background: #fff;
              color: #323232;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 576px) {
  #home {
    background: #282424;
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: top;
    justify-content: flex-start;

    header {
      padding: 16px 0 0 0;
      .container {
        position: relative;
        z-index: 5;
        align-items: flex-start;
        height: 35vh;

        .logo {
          width: 80px;
        }

        nav {
          ul {
            li {
              &:nth-child(1) {
                a {
                  transition: all 0.3s;
                  &:hover {
                    background: rgba(255, 255, 255, 0.05);
                  }
                }
              }

              a {
                align-items: center;
                justify-content: center;
                width: 36px;
                height: 36px;
                padding: 0;
                svg {
                  path {
                    transition: all 0.3s;
                  }
                }
                &:hover {
                  svg {
                    path {
                      fill: #fff;
                    }
                  }
                }
              }
            }
          }
        }
      }

      .cover-mobile {
        display: block;
        position: absolute;
        top: 0;
        width: 100%;
        height: 36vh;
        overflow: hidden;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }

        &:after {
          content: "";
          position: absolute;
          bottom: 0;
          height: 100%;
          background: rgb(40, 36, 36);
          background: linear-gradient(
            0deg,
            rgba(40, 36, 36, 1) 0%,
            rgba(106, 106, 106, 0) 46%
          );
          width: 100%;
        }
      }
    }

    .middle-infos {
      height: 40vh;
      .container {
        position: relative;
        z-index: 5;
      }

      .top {
        p {
          font-size: 10px;
        }
        small {
          margin: 0 4px;
          font-size: 10px;
        }
      }

      h1 {
        margin-top: 4px;
        font-size: 28px;
      }

      .description {
        line-height: 24px;
        margin-top: 8px;
        margin-bottom: 16px;
        font-size: 14px;
      }

      .btn-infos {
        position: initial;
        width: calc(100% - 30px);
        height: 56px;
        padding-left: 0;
        margin: 8px 15px;
        animation: fromBottom 1s;
        svg {
          width: 20px;
          margin-right: 4px;

          path {
            transition: all 0.3s;
          }
        }

        p {
          display: flex;
          opacity: 1;
        }

        &:after {
          content: "";
          display: none;
        }

        &:hover {
          color: #fff;
          background: #323232;

          width: initial;
          padding-left: 0;

          svg {
            path {
              fill: #fff;
            }
          }
        }
      }
    }

    .bottom-infos {
      padding: 8px 0 8px 0;
      position: absolute;
      width: 100%;
      bottom: 0;
      .container {
        align-items: flex-end;
        .social {
          flex-direction: column;
        }
        a {
          img {
            width: 20px;
          }

          &:nth-child(2) {
            margin: 8px 0;
          }
        }

        .bottom-middle {
          margin-bottom: 16px;
          a {
            width: 32px;
            height: 32px;

            img {
              width: 8px;
            }
          }

          p {
            font-size: 14px;
            input {
              font-size: 14px;
            }
          }
        }
      }
    }
  }
}

@media (max-width: 350px) {
  #home {
    header {
      padding: 12px 0 0 0;
      .container {
        height: 32vh;
        .logo {
          width: 70px;
        }
      }

      .cover-mobile {
        height: 32vh;
      }
    }

    .middle-infos {
      height: 45vh;

      .top {
        p {
          font-size: 8px;
          letter-spacing: 0.05em;
        }
        small {
          font-size: 8px;
        }
      }

      h1 {
        margin-top: 4px;
        font-size: 24px;
      }

      .description {
        font-size: 13px;
        line-height: 20px;
      }
    }
  }
}
</style>